import * as React from 'react';
import { sanitizeUrl } from '@ali/4ever-utils';

export default function link(attrs: Record<string, any>, path: number[]) {
  const { metadata } = attrs;
  const { href, text } = metadata;

  const normalizedText = text || href;

  return (
    <a href={sanitizeUrl(href)} target="_blank" rel="noopener noreferrer" key={path.toString()}>
      {normalizedText}
    </a>
  );
}
